@import url(https://fonts.googleapis.com/css?family=Lato|Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* font-family: 'Lato', sans-serif;
font-family: 'Raleway', sans-serif; */

html {
  font-size: 8px;
}

body {
  margin: auto
}

h1 {
  font-family: 'Lato', sans-serif;
  font-size: 5.5rem;
  font-weight: 300;
  color: #2E2424;
  text-shadow: #A7A7A7 1px 3px 2px;
  margin-top: 0px;
}

h2 {
  font-family: 'Lato', sans-serif;
  font-size: 2.5rem;
  font-weight: 300;
  color: #80C79E;
  margin-bottom: 0px;
}

h3 {
  font-family: 'Lato', sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #2E2424;
  margin-bottom: 0px;
}

.hidden {
  display: none;
}


@media screen and (min-width: 768px) {
  html {
    font-size: 12px;
  }
}

@media screen and (min-width: 1366px) {
  html {
    font-size: 16px;
  }
}
header {
  height: 8vh;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  margin-bottom: 15vh;
}

@media screen and (min-width: 768px) {
  header {
    height: 8vh;
    width: 90%;
  }
}

@media screen and (min-width: 1366px) {
  header {
    height: 8vh;
    width: 80%;
  }
}
.logo {
  flex: 1 1;
  margin-left: 3vw;
  margin-top: 3vh;
}

.logo-text {
  color: #2E2424;
  margin: 0px;
  display: block;
  width: 4.5rem;
  text-align: right;
  border-bottom: 0.1rem solid #80C79E;
}

@media screen and (min-width: 768px) {
  .logo {
    margin-left: 5vw;
    margin-top: 2vh;
  }
}

@media screen and (min-width: 1366px) {
  .logo {
    margin-left: 8vw;
    margin-top: 1vh;
  }
}
span {
  border-top: .2rem solid #2E2424;
  display: block;
  width: 3.5rem;
  margin-bottom: 1rem;
}

.collapsible-menu {
  position: absolute;
  display: block;
  right: 0px;
  top: 8vh;
}

.burger-menu {
  cursor: pointer;
  margin-top: 3vh;
  margin-right: 3vw;
}

@media screen and (min-width: 768px) {
  .burger-menu {
    display: none;
  }

  .collapsible-menu {
    display: none;
  }
}

.nav-links {
  display: none;
  margin-top: 4vh;
}

.nav-links > li {
  flex: 1 1;
  display: list-item;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 300;
  color: #2E2424;
  margin-bottom: 0px;
}

@media screen and (min-width: 768px) {
  .nav-links {
    list-style: none;
    list-style-image: none;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
  }
}

.collapsible-links {
  list-style-image: none;
  display: flex;
  flex-direction: column;
  background-color: #2E2424;
  width: 100vw;
  justify-content: center;
  padding-top: 1em;
  padding-bottom: 1em;
  border-top: .2rem solid #80C79E;
  text-align: center;
}

.collapsible-links > li {
  text-align: center;
  flex: 1 1;
  display: list-item;
  font-family: 'Lato', sanfs-serif;
  font-size: 2rem;
  font-weight: 300;
  color: #FFFFFF;
  margin-bottom: 3rem;
  width: 100vw;
} 
#main-headline {
  width: 80vw;
  margin: auto;
}

@media screen and (min-width: 768px) {
  #main-headline {
    margin-left: 10vw;
  }
}

@media screen and (min-width: 1366px) {
  #main-headline {
    margin-left: 20vw;
  }
}
